@font-face {
    font-family: "Future";
    src: url("Kenney_Future_Narrow.ttf");
}

.navbar {
    width: 100%;
    background-color: #555;
    overflow: auto;
}
  
.navbar a {
    float: left;
    padding: 12px;
    color: white;
    text-decoration: none;
    font-size: 17px;
}
  
.navbar a:hover {
    background-color: #000;
}
  
.active {
    background-color: #80BE1F;
}
  
